<template>
<div>
    <!-- <NavBar/> -->
    <main class="page">
        <section class="container clean-block clean-catalog dark">
                <div class="block-heading">
                    <img class="img-responsive" src="../../static/assets/img/mssn.png" style="width:100px">
                    <h3>MSSN FULAFIA DIGITAL LIBRARY</h3>
                </div>
                <div class="form-group">
                  <input type="search" placeholder="Enter name of book to search" class="form-control" aria-describedby="helpId">
                </div>
                <div class="row">
                        <div v-if="errored" class="col-md-12 col-lg-12 text-center alert alert-danger" role="alert">
                            <p style="font-size: 100px;" class="text-center">
                                <i class="fa fa-exclamation-circle" aria-hidden="true"></i>
                                </p>
                                <strong>Error:</strong> {{videos}}
                        </div>
                        <!-- <div v-else> -->
                            <div v-else-if="loading" class="text-center " style="font-size:30px">
                                <!-- <div > -->
                                    Please wait...<br><i class="fa fa-refresh fa-spin" aria-hidden="true"></i>
                                <!-- </div> -->
                            </div>
                            <div v-else :key="video.id" v-for="(video, i) in videos" class="col-md-3 col-lg-3 text-center">
                                <div v-if="i < videosToDisplay.count">
                                    <div class="embed-responsive embed-responsive-16by9">
                                        <i class="fa fa-file-pdf-o embed-responsive-item" style="font-size: 170px;color:red" aria-hidden="true"></i>
                                    </div>
                                    <div class="card-body info">
                                        <h6 class="card-title">{{"PDF DOC "+i}}</h6>
                                        <button type="button" class="btn btn-primary btn-block btn-success">Download <i class="fa fa-download" aria-hidden="true"></i></button>
                                        <button type="button" class="btn btn-primary btn-block">Preview <i class="fa fa-eye" aria-hidden="true"></i></button>
                                        <div v-html="shareButtons(video.title, video.url)"></div>
                                        <a target="_blank" href="mailto:tasiukwaplong@gmail.com">
                                            Contact uploader <i class="fa fa-envelope" aria-hidden="true"></i>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        <!-- </div> -->
                </div>
                <nav>
                    <ul class="pagination" v-if="videos.length > videosToDisplay.count" v-show="typeof(videos) === 'object'">
                        <li class="page-item active">
                            <button type="button" @click="videosToDisplay.count += 4" class="btn btn-primary btn-lg">Show more</button>
                        </li>
                    </ul>
                    <ul class="pagination" v-else v-show="typeof(videos) === 'object'">
                        <li class="page-item active">
                            <em>That's all we have. We're glad you came this far 😊...</em>
                        </li>
                    </ul>
                </nav>
        </section>
    </main>
    <!-- Modal -->
    <div class="modal fade" id="modelId" tabindex="-1" role="dialog" aria-labelledby="modelTitleId" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">{{selectedVideo.title}}</h5>
                        <button type="button"  class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                </div>
                <div class="modal-body">
                    <div class="form-check" v-if="selectedVideo.links">
                    <div
                      v-show="selectedVideoRaw.length > 10"
                      v-html="selectedVideoRaw"></div>
                    </div>
                    <div v-else>
                        <div class="form-check" v-if="downloadLoading">
                            Opening video player. Please wait....
                            <i class="fa fa-refresh fa-spin" aria-hidden="true"></i>
                            <!-- selectedVideoProp(selectedVideo.url) -->
                        </div>
                        <div class="row form-check" v-else>
                            <div class="alert alert-danger text-center" role="alert">
                                <i style="color:red; font-size: 20px" class="fa fa-exclamation-triangle" aria-hidden="true"></i><br>
                                <strong>Error: </strong> {{selectedVideo.error}}
                            </div>
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                    <!-- <button type="button" v-show="selectedVideo.links" class="btn btn-success">Download now <i class="fa fa-download" aria-hidden="true"></i> </button> -->
                </div>
            </div>
        </div>
    </div>
<!-- <Foot /> -->
</div>
</template>

<script>
import vc from '../controller/viralvideo.js'
import {mixin} from '../controller/mixins'
import NavBar from './NavBar'
import Foot from './Foot'
export default {
  name: 'Home',
  data () {
    return {
      errored: false,
      loading: true,
      videos: [],
      selectedVideo: {},
      downloadLoading: false,
      selectedVideoRaw: '',
      videosToDisplay: {
        count: 12,
        category: ''
      },
      images: [],
      categories: {}
    }
  },
  mixins: [mixin],
  components: {NavBar, Foot},
  mounted: async function () {
    // before component loads
    const data = await vc.getVideos()
    this.loading = (data === undefined)
    this.errored = data.errored
    this.videos = data.message
    if (typeof (this.videos) === 'object') {
      this.videos.forEach((d) => {
        if (d.image_src.length >= 5 && this.images.length < 5) this.images.push(d.image_src)
        if (d.sub_linked.length >= 1) {
          // if video category has sub category
          (this.categories[d.category] === undefined)
            ? this.categories[d.category] = [d.sub_linked]
            : this.categories[d.category].push(d.sub_linked)
        } else {
          if (this.categories[d.category] === undefined) this.categories[d.category] = []
        }
      })
    }
  }
}
</script>



